import React from 'react';
import PropTypes from 'prop-types';
import {Tab, Tabs} from '@blueprintjs/core';
import "./index.css";
import {withRouter} from "react-router-dom";
import queryString from 'query-string';
import _isEqual from 'lodash/isEqual';


class Reports extends React.Component{

    state = {
        tabId: "Machine Locations History Report"
    }

    static getDerivedStateFromProps(nextProps, prevState){
        const {tabId: prevTabId} = prevState;
        const {location: {search: newSearch}} = nextProps;
        const {type: tabId} = queryString.parse(newSearch);

        if(_isEqual(prevTabId, tabId))
            return null;

        return { tabId };
    }

    render(){

        return (
            <div className="Reports__page">
            
                <Tabs
                    vertical
                    selectedTabId={this.state.tabId}
                    onChange={(nextTabId, prevTabId) => this._handleChangeOfTab(nextTabId, prevTabId)}
                    large
                    renderActiveTabPanelOnly id={"reportsTabs"}>

                    <Tab id={"Machine Locations History Report"}
                            title="Machine Locations HistoryReport" panel={<div>Machine Locations History Report</div>} />

                    <Tab id={"Center Deployments"}
                            title="Center Deployments" panel={<div>Center Deployments</div>} />

                    <Tab id={"Machine Deployments"}
                            title="Machine Deployments" panel={<div>Machine Deployments</div>} />

                    <Tab id={"Consumables in Stock"}
                            title="Consumables in Stock" panel={<div>Consumables in Stock</div>} />

                    <Tab id={"Consumables Allocation Report"}
                            title="Consumables Allocation Report" panel={<div>Consumables Allocation Report</div>} />

                    <Tabs.Expander />

                </Tabs>

            </div>
        );
    }

    _handleChangeOfTab = (nexTabId, prevTabId) => {
        if(nexTabId !== prevTabId){
            this.props.history.push(`?type=${nexTabId}`);
        }
    };

    static propTypes = {
        history: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
    };

}

export default withRouter(Reports);


